import { forwardRef } from 'react';
import type { FormProps } from '@remix-run/react';
import { Form as RemixForm } from '@remix-run/react';

type RemixFormFnProps = FormProps & React.RefAttributes<HTMLFormElement>;

type RemixFormFnRef = React.ForwardedRef<HTMLFormElement>;

function RemixFormFn(props: RemixFormFnProps, ref: RemixFormFnRef) {
  return (
    <RemixForm {...props} ref={ref} />
  );
}

type FormRowFnProps = { spacing?: 'sm'|'md'|'lg'} & React.HTMLAttributes<HTMLDivElement>;

type FormRowFnRef = React.ForwardedRef<HTMLDivElement>;

function FormRowFn(props: FormRowFnProps, ref: FormRowFnRef) {
  return (
    <div
      ref={ref}
      className={`${
        props.className ? `${props.className} ` : ''
      }mx-auto max-w-lg mb-5`}>
      {props.children}
    </div>
  );
}

export const Form = Object.assign(forwardRef(RemixFormFn), {
  Row: forwardRef(FormRowFn),
});
